import vip_conf from '../../assets/configs/vip/conf.json'
import avaliable_conf from '../../assets/configs/pay/available.json'
import rightpanel from './rightpanel'

export default {
  name: "vipcode",
  components: {
    rightpanel
  },
  data() {
    return {
      loading: false,
      code: ''
    }
  },
  created() {


  },
  methods: {
    on_submit: function () {
      if (this.code.length <= 12) {
        return this.$message('请输入卡密', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
      }

      this.loading = true;
      var post_data = {
        'code': this.code,
        'action': 'check'
      };
      this.$axios.post('/app/active/code', post_data).then((response) => {
        this.loading = false;
        var response_data = response.data;
        console.log("response_data:", response_data)
        var vip_code = response_data.vip_code;
        if (vip_code) {
          this.$message('已成功激活', {
            theme: "error",
            position: "top-left",
            duration: 5000
          });
          setTimeout(() => {
            this.$router.push('/user/info');
          }, 1000);

        }
      }).catch((error) => {
        console.log('error:', error);
        this.loading = false;

      });
    },
  }
}
